@import url(https://fonts.googleapis.com/css2?family=Rasa:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: left;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
}

.center {
  text-align: center;
}

* {
  box-sizing: border-box;
}

.input {
  width: 100%;
  max-width: 30em;
  margin: 10px;
  padding: 12px;
  font-family: 'Rasa';
  background: #E6E6E6;
}

p {
  margin: 2px;
  padding: 2px;
  font-family: 'Rasa';
}

li {
  font-family: 'Rasa';
  margin: 5px;
}

.text {
  color: #000000;
  font-family: 'Rasa', serif;
  font-size: 1em;
}

h1, h2 {
  color: #000000;
  font-family: 'Rasa', serif;
  font-weight: 400;
}

/* Grid classes */
.row {
  width: 100%;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

/* For mobile phones: */
[class*="col-"] {
  width: 100%;
  float: left;
  padding: 10px;
}

@media only screen and (min-width: 600px) {
  /* For tablets: */
  .col-s-1 {width: 8.33%;}
  .col-s-2 {width: 16.66%;}
  .col-s-3 {width: 25%;}
  .col-s-4 {width: 33.33%;}
  .col-s-5 {width: 41.66%;}
  .col-s-6 {width: 50%;}
  .col-s-7 {width: 58.33%;}
  .col-s-8 {width: 66.66%;}
  .col-s-9 {width: 75%;}
  .col-s-10 {width: 83.33%;}
  .col-s-11 {width: 91.66%;}
  .col-s-12 {width: 100%;}
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .col-1 {width: 8.33%;}
  .col-2 {width: 16.66%;}
  .col-3 {width: 25%;}
  .col-4 {width: 33.33%;}
  .col-5 {width: 41.66%;}
  .col-6 {width: 50%;}
  .col-7 {width: 58.33%;}
  .col-8 {width: 66.66%;}
  .col-9 {width: 75%;}
  .col-10 {width: 83.33%;}
  .col-11 {width: 91.66%;}
  .col-12 {width: 100%;}
}
.header-container {
    width: 100%;
    height: auto;
    padding: 0.2em;
    background: #000000;
}

@media only screen and (min-width: 768px) {
    .header-container {
        position: fixed;
        width: 100%;
        height: auto;
        padding: 0.2em;
        background: #000000;
    }
}

.law-logo {
    width: 25;
}

.header-name {
    color: #FFFFFF;
    font-size: 1.4em;
    font-family: 'Rasa';
    margin: 1em;
}

a {
    text-decoration: none;
}

.header-link {
    color: #E6E6E6;
    font-size: 1.2em;
    font-family: 'Rasa';
    margin-left: 1em;
    margin-right: 1em;
}

.header-link:hover, .header-link:active {
    color: #FFFFFF;
    font-weight: bold;
}

.header-link:visited {
    color: #E6E6E6;
}
/* Smartphones */
.destaque-background {
    width: 100%;
    height: 20em;
    background-image: url(/static/media/background-phone.ed6800d1.png);
}

.destaque-box {
    margin-top: 12em;
}

.destaque-name {
    font-size: 2em;
    font-weight: bold;
}

.destaque-oab {
    font-size: 1.5em;
}

/* Desktops */
@media only screen and (min-width: 768px) {
    .destaque-background {
        width: 100%;
        height: 30em;
        background-image: url(/static/media/background.19acb405.png);
    }

    .destaque-box {
        margin-top: 22em;
    }

    .destaque-name {
        font-size: 3em;
        font-weight: bold;
    }
    
    .destaque-oab {
        font-size: 2em;
    }
}
.separator {
    width: 90%;
    border: 2px solid black;
}
.content-title {
    font-size: 2em;
    margin-left: 1em;
}

/* Smartphone */
.content-title-container {
    width: 100%;
    padding: 0em;
    margin-bottom: 1em;
}

/* Desktop */
@media only screen and (min-width: 768px) {
    .content-title-container {
        width: 100%;
        padding: 1em;
        margin-bottom: 1em;
    }
}

#QuemSou {
    background-image: url(/static/media/books.5bb9b4c1.png);
}

#OQueFaco {
    background-image: url(/static/media/office.916c97ab.png);
}

#Atuacao {
    background-image: url(/static/media/writing.6cd14fea.png);
}
.footer-container {
    width: 100%;
    padding: 20px;
    background: #000000;
}

.footer-title {
    color: #FFFFFF;
    margin-left: 1em;
}

.footer-text {
    color: #FFFFFF;
}

.footer-label {
    color: #e6e6e6;
}

.footer-button {
    padding: 10px;
    padding-left: 30px;
    padding-right: 30px;
    margin: 10px;
    background: #000000;
    color: #e6e6e6;
    font-family: 'Rasa';
    border: 1px solid #e6e6e6;
}

.footer-button:hover {
    background: #E6E6E6;
    color: black;
    border: 1px solid black;
}
