@import url('https://fonts.googleapis.com/css2?family=Rasa:wght@300&display=swap');

.App {
  text-align: left;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
}

.center {
  text-align: center;
}

* {
  box-sizing: border-box;
}

.input {
  width: 100%;
  max-width: 30em;
  margin: 10px;
  padding: 12px;
  font-family: 'Rasa';
  background: #E6E6E6;
}

p {
  margin: 2px;
  padding: 2px;
  font-family: 'Rasa';
}

li {
  font-family: 'Rasa';
  margin: 5px;
}

.text {
  color: #000000;
  font-family: 'Rasa', serif;
  font-size: 1em;
}

h1, h2 {
  color: #000000;
  font-family: 'Rasa', serif;
  font-weight: 400;
}

/* Grid classes */
.row {
  width: 100%;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

/* For mobile phones: */
[class*="col-"] {
  width: 100%;
  float: left;
  padding: 10px;
}

@media only screen and (min-width: 600px) {
  /* For tablets: */
  .col-s-1 {width: 8.33%;}
  .col-s-2 {width: 16.66%;}
  .col-s-3 {width: 25%;}
  .col-s-4 {width: 33.33%;}
  .col-s-5 {width: 41.66%;}
  .col-s-6 {width: 50%;}
  .col-s-7 {width: 58.33%;}
  .col-s-8 {width: 66.66%;}
  .col-s-9 {width: 75%;}
  .col-s-10 {width: 83.33%;}
  .col-s-11 {width: 91.66%;}
  .col-s-12 {width: 100%;}
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .col-1 {width: 8.33%;}
  .col-2 {width: 16.66%;}
  .col-3 {width: 25%;}
  .col-4 {width: 33.33%;}
  .col-5 {width: 41.66%;}
  .col-6 {width: 50%;}
  .col-7 {width: 58.33%;}
  .col-8 {width: 66.66%;}
  .col-9 {width: 75%;}
  .col-10 {width: 83.33%;}
  .col-11 {width: 91.66%;}
  .col-12 {width: 100%;}
}